<template>
  <div>

      <b-form @submit="submit">
        <b-card bg-variant="light" class="mb-3">
          <b-form-group
            id="input-group-user-register-role"
            label="Register User"
            label-for="input-user-register-role"
            label-size="lg"
            label-class="font-weight-bold pt-0"
          >
            <b-form-select
              id="input-user-register-role"
              v-model="user.role"
              :options="options"
              required
            >
            </b-form-select>
          </b-form-group>

          <b-form-group
            id="fieldset-user-register-firstname"
            label="First Name"
            label-for="input-user-register-firstname"
          >
            <b-form-input
              id="input-user-register-firstname"
              v-model="user.firstname"
              placeholder="Enter user's given name"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="fieldset-user-register-lastname"
            label="Last Name"
            label-for="input-user-register-lastname"
          >
            <b-form-input
              id="input-user-register-lastname"
              v-model="user.lastname"
              placeholder="Enter user's last name"
              required
            />
          </b-form-group>

          <b-form-group
            id="input-group-user-register-email"
            label="Email"
            label-for="input-user-register-email"
          >
            <b-form-input
              type="email"
              id="input-user-register-email"
              v-model="user.email"
              placeholder="Enter user's email"
              :state="isEmail"
              required
            >
            </b-form-input>

            <b-form-invalid-feedback :state="isEmail">
              Enter a valid email
            </b-form-invalid-feedback>

          </b-form-group>

          <b-form-group
            id="input-group-user-register-password"
            label="Create New Password"
            label-for="input-user-register-password"
          >
            <b-form-input
              type="password"
              id="input-user-register-password"
              v-model="user.password"
              placeholder="Enter user's password"
              required
              autocomplete="current_password"
            >
            </b-form-input>

            <b-button class="m-2" type="submit" variant="primary">Register</b-button>

          </b-form-group>
          <b-alert v-if="message" show :variant="error ? 'danger' : 'success'">{{message}}</b-alert>
        </b-card>
      </b-form>

  </div>
</template>

<script>

import {validateEmail} from '@/services/data-services'
import formServices from '@/services/form-services'
import api from '../../services/api-services';

const initUserData = {
    guid: 'test',
    role: 'adj',
    firstname: '',
    lastname: '',
    email: '',
    password: ''
}

export default {
  name: "user-input",
  props: {
    init: Object
  },
  data() {
    return {
      user: this.init ? this.init : initUserData,
      error: false,
      message: '',
      options: formServices.get('roles')
    }
  },
  computed: {
    isEmail () {
      return validateEmail(this.user.email)
    }
  },
  methods: {
      async submit(event) {
      event.preventDefault();
      try {
        const response = await api.post('auth/register', this.user);
        console.log(response)
        this.error = false
        this.message = 'Successfully registered user!'
      } catch (err) {
        console.error(err);
        this.error = true
        this.message = 'Registration failed.'
      }
    }
  }
};
</script>

