<template>
  <div>
    <overview title="Overview" header="Legacy" lead="Premier's Awards Nomination for Legacy">
      <template v-slot:text>
              <p>This award recognizes individuals who have made exceptional and lasting contributions to the BC Public Service and/or the province during a public service career of at least 15 years. The contributions may cover a nominee’s entire career or portions of it, provided the contribution has a lasting and ongoing positive impact.</p>
              <p>Considerations may include but are not limited to</p>
                <ul>
                  <li>Impact on peers, the internal operations and/or external organizations</li>
                  <li>Contributions to the development or application of best practices in their field of expertise</li>
                  <li>Experimental or innovative mindset whereby openness to change, taking calculated risks and challenging the status quo to try something new resulted in a measurable outcome</li>
                  <li>Embodying the ideal vision of leadership within the BC Public Service</li>
                  <li>Commitment to diversity and inclusion in both the work environment and in the development of programs, policies and services</li>
                  <li>Advocating meaningful engagement and learning with Indigenous peoples to support and facilitate reconciliation</li>
                  <li>Superior leadership through complex situations including large-scale initiatives, managing and mitigating risks, creatively solving problems and implementing solutions</li>
                  <li>Achieving outcomes despite constraints such as technical, organizational and financial</li>
                  <li>Commitment to succession management through knowledge transfer, support for continuous learning and mentorship</li>
                </ul>
      </template>
    </overview>

    <!-- begin nomination form -->
    <b-form @submit="onSubmit" @reset="onReset">

      <organization
        :init="nomination.organization"
        @update="updateOrganization($event)"
      />

      <nominees
        :init="nomination.nominees"
        @update="updateNominees($event)"
        type="single"
      />

      <nominators
        :init="nomination.nominators"
        @update="updateNominators($event)"
      />

      <contacts
        :init="nomination.contacts"
        @update="updateContacts($event)"
      />

      <acknowledgement
        :init="nomination.acknowledgement"
        @update="updateAcknowledgement($event)"
      />

      <evaluation
        :init="nomination.evaluation"
        @update="updateEvaluation($event)">
        <template v-slot:summary_overview></template>
        <template v-slot:context_overview></template>
        <template v-slot:complexity_overview>
          <div>
            <p>Describe the scope and scale of the evidence-based or evidence-informed approach to developing the policy or program including, but not limited to</p>
            <ul>
              <li>The reason and evidence for the change or initiative necessitating the development of the policy or program</li>
              <li>Risks encountered and overcome</li>
              <li>Constraints such as time frame, financial or existing policy</li>
              <li>Challenges around aligning stakeholder or partner group interests</li>
              <li>Technical or organizational difficulties identified and addressed</li>
            </ul>
          </div>
        </template>

        <template v-slot:valuing_people_overview>
          <p>Identify and describe how the employee demonstrates their leadership in supporting the workplace including, but not limited to</p>
          <ul>
            <li>Innovative problem solving, team building and collaborating with colleagues at all levels of the organization</li>
            <li>Actively enhancing the quality of the workplace through commitment to diversity and inclusion in both the work environment and the development of programs, policies and services</li>
            <li>Integrity and commitment to ethical conduct and decision making</li>
          </ul>
        </template>

        <template v-slot:commitment_overview>
          <p>Identify and describe how the employee demonstrates their leadership in operations including, but not limited to</p>
          <ul>
            <li>Commitment to and pride in the BC Public Service by contributions enhancing its reputation</li>
            <li>Ability to constructively articulate a strong case for change by challenging assumptions, timelines and recommendations to ensure they are driven by citizens’ needs rather than administrative convenience</li>
            <li>Supporting innovation, new technology and best practices by modelling a culture of curiosity, collaborating, encouraging dialogue and inviting competing ideas</li>
            <li>Making decisions with considerations of the long-term impacts and context, anticipating emerging priorities as well as the current need</li>
            <li>Thoughtful risk management and ability to accept failure by learning, improving and moving forward</li>
            <li>Success through measurable project/program outcomes</li>
          </ul>
        </template>
        <template v-slot:impact_overview>
          <p>Describe and use metrics (if applicable) to support how the employee exemplifies the future of corporate leadership as demonstrated by, but not limited to</p>
          <ul>
            <li>Effect on employees, clients, the public service and/or citizens of British Columbia</li>
            <li>Improving the quality of the workplace for employees by contributing to the enhancement of diversity, health and safety, workplace culture and employee development</li>
            <li>Supporting innovation, new technology and best practices, resulting in the improvement of workplace processes such as reduced costs, increased efficiencies, or safety</li>
            <li>Measurable benefits to their organization(s) and/or the citizens through process improvements to transform business practices, and/or sustainable revenue generation or savings.</li>
          </ul>
        </template>
      </evaluation>

      <attachments
        :init="nomination.attachments"
        @update="updateAttachments($event)" />

      <submit :id="id" label="Submit the Nomination" :error="error" />
    </b-form>

  </div><!-- end nomination form -->
</template>

<script>

import overview from '../common/Accordion'
import organization from '../inputs/InputOrganization'
import nominees from '../inputs/InputNominees'
import nominators from '../inputs/InputNominators'
import contacts from '../inputs/InputContacts'
import acknowledgement from '../inputs/InputAcknowledge'
import evaluation from '../inputs/InputEvaluation'
import attachments from '../inputs/InputAttachments'
import submit from '../inputs/InputSubmit'
import {genID} from '@/services/data-services'
import {postData} from '@/services/api-services'

export default {
  name: "nomination-emerging-leader",
  components: {
    overview,
    organization,
    nominees,
    nominators,
    contacts,
    acknowledgement,
    evaluation,
    attachments,
    submit
  },
  data() {
    return {
      id: genID(),
      type: 'emerging-leader',
      nomination: {
        organization: null,
        nominees: [],
        nominators: [],
        contacts: {},
        acknowledgement: 'not_accepted',
        evaluation: {},
        attachments: []
      },
      error: ''
    }
  },
  methods : {
    updateOrganization(event) {
      console.log(event)
      this.nomination.organization = event
    },
    updateNominees(event) {
      console.log(event)
      this.nomination.nominees = event
    },
    updateNominators(event) {
      console.log(event)
      this.nomination.nominators = event
    },
    updateContacts(event) {
      console.log(event)
      this.nomination.contacts = event
    },
    updateAcknowledgement(event) {
      console.log(event)
      this.nomination.acknowledgement = event
    },
    updateEvaluation(event) {
      console.log(event)
      this.nomination.evaluation = event
    },
    updateAttachments(event) {
      console.log(event)
      this.nomination.attachments = event
    },
    onSubmit(event) {
      event.preventDefault();
      postData(`nomination/${this.type}/create`, this.nomination)
        .catch((err) => {
          console.error(err);
          this.error = 'Form data is not valid.'
        })
    },
    onReset(event) {
      event.preventDefault()
      this.nomination = this.$props.init
      // Trick to reset/clear native browser form validation state
      this.show = false
      this.$nextTick(() => {
        this.show = true
      })
    }
  }
};
</script>

