<template>
  <div>
    <b-card bg-variant="light" class="mb-3">
      <b-form-group
        id="input-group-organization"
        :label="label ? label : 'Name of Ministry or eligible organization sponsoring this application'"
        label-for="input-organization"
        label-size="lg"
        label-class="font-weight-bold pt-0"
      >
        <b-form-select
          id="input-organization"
          v-model="selected"
          :options="options"
          @change="update()"
          :state="validation"
        >
        </b-form-select>
        <b-form-invalid-feedback :state="validation">
          Please make a selection.
        </b-form-invalid-feedback>
      </b-form-group>

    </b-card>
  </div>
</template>

<script>

import formServices from '../../services/form-services'

export default {
  name: "organization-input",
  props: {
    label: String,
    init: String,
  },
  data() {
    return {
      selected: this.$props.init,
      options: formServices.get('organizations')
    }
  },
  computed: {
    validation () {
      return !!this.selected
    }
  },
  methods: {
    update() {
      this.$emit('update', this.selected)
    }
  }
};

</script>
