/**
 * Services and utilities for forms
 * **/

export default {

  /**
   * get enumerated data by key
   * **/

  get: function get(key) {
    const schemaData = {
      "roles": [
        {value: 'adj', text: 'Adjudicator'},
        {value: 'adm', text: 'Administrator'}
      ],
      "organizations": [
        {value: null, text: 'Please select an eligible Ministry or organization'},
        {value: 'min-1', text: 'Ministry 1'},
        {value: 'min-2', text: 'Ministry 2'},
        {value: 'min-3', text: 'Ministry 3'},
      ],
      "nominatorTypes": [
        {value: null, text: 'Please select a nominator type'},
        {value: 'partner', text: 'Partner'},
        {value: 'nominator', text: 'Nominator'},
      ]
    };
    return schemaData[key] !== 'undefined' ? schemaData[key] : null;
  }
}


