<template>
  <div class="mb-3">
    <b-card bg-variant="light" class="mb-3">
      <b-form-group
        v-for="(nominator, counter) in data"
        v-bind:key="counter"
        label-cols-lg="3"
        :label="`Nominator ${counter > 0 ? counter + 1 : ''}`"
        label-size="lg"
        label-class="font-weight-bold pt-0"
        class="mb-4"
      >
        <slot name="overview" v-if="counter === 0">
          <p>The nominator is the person who will be identified in the video if the nomination is chosen as a finalist. Typically, the nominator is someone from the Executive who has been part of the nomination. The nominator and nomination contact can be the same person, but it is not recommended as there is considerable communication between the nomination contact and the Premier’s Awards team. Please fill out the section below if there is a secondary or co-nominator for this nomination.</p>
        </slot>

        <b-form-group
          :id="`fieldset-nominators-${counter}-firstname`"
          label="First Name"
          :label-for="`input-nominators-${counter}-firstname`"
        >
          <b-form-input
            :id="`input-nominators-${counter}-firstname`"
            v-model="nominator.firstname"
            placeholder="Enter the nominator's given name"
            @change="update()"
            :state="counter === 0 ? validate(counter, 'firstname') : null"
          >
          </b-form-input>
          <b-form-invalid-feedback :state="counter === 0 ? validate(counter, 'firstname') : null">
            A first name is required
          </b-form-invalid-feedback>
        </b-form-group>

        <b-form-group
          :id="`fieldset-nominators-${counter}-lastname`"
          label="Last Name"
          :label-for="`input-nominators-${counter}-lastname`"
        >
          <b-form-input
            :id="`input-nominators-${counter}-lastname`"
            v-model="nominator.lastname"
            placeholder="Enter the nominator's last name"
            @change="update()"
            :state="counter === 0 ? validate(counter, 'lastname') : null"
          />
          <b-form-invalid-feedback v-if="counter === 0 || hasAdditional" :state="validate(counter, 'lastname')">
            A last name is required
          </b-form-invalid-feedback>
        </b-form-group>

        <b-form-group
          :id="`fieldset-nominators-${counter}-title`"
          label="Nominator Title"
          :label-for="`input-nominators-${counter}-title`"
        >
          <b-form-input
            :id="`input-nominators-${counter}-title`"
            v-model="nominator.title"
            placeholder="Enter the nominator's title"
            @change="update()"
            :state="counter === 0 ? validate(counter, 'title') : null"
          ></b-form-input>
            <b-form-invalid-feedback :state="validate(counter, 'title')">
              A title is required
            </b-form-invalid-feedback>

        </b-form-group>

        <b-form-group
          :id="`fieldset-nominators-${counter}-email`"
          label="Email"
          :label-for="`input-nominators-${counter}-email`"
        >
          <b-form-input
            type="email"
            :id="`input-nominators-${counter}-email`"
            v-model="nominator.email"
            placeholder="Enter the nominator's contact email"
            @change="update()"
            :state="counter === 0 ? validateEmail : null"
          />
          <b-form-invalid-feedback :state="counter === 0 ? validateEmail : null">
            A valid email address is required
          </b-form-invalid-feedback>
        </b-form-group>

      </b-form-group>
    </b-card>

  </div>
</template>

<script>

import {validateEmail} from '@/services/data-services'

const noinit = [
  { firstname: '', lastname: '', title: '', email: '' },
  { firstname: '', lastname: '', title: '', email: '' }
]

export default {
  name: "nominators-input",
  props: {
    init: Array,
  },
  data () {
    return {
      data: this.$props.init.length === 2 ? this.$props.init : noinit
    }
  },
  computed: {
    validate(){
      return (id, field) => {
        const fields = {
          firstname: ()=>{
            return id === 0 ? this.data[0].firstname.length > 0 : this.hasAdditional
          },
          lastname: ()=>{
            return id === 0 ? this.data[0].lastname.length > 0 : this.hasAdditional
          },
          title: ()=>{
            return id === 0 ? this.data[0].title.length > 0 : this.hasAdditional
          }
        }
        // console.log(fields[field](), field, id, this.data[id].title)
        if (fields[field] === 'undefined') return false;
        return !!fields[field]();
      }
    },
    hasAdditional () {
      return !!this.data[1].firstname || !!this.data[1].lastname || !!this.data[1].title || !!this.data[1].email
    },
    validateEmail () {
      return validateEmail(this.data[0].email)
    }
  },
  methods: {
    update () {
      this.$emit('update', this.data)
    },
    hasCompletedAdditional () {
      return this.data[1].firstname && this.data[1].lastname && this.data[1].title && this.data[1].email
    }
  }
}
</script>

