<template>
  <div>

    <!-- Nomination Attachments -->
    <b-card bg-variant="light" class="mb-3">
      <b-form-group
        id="input-group-attachments"
        label="Nomination File Attachments"
        label-for="input-attachments"
        label-size="lg"
        label-class="font-weight-bold pt-0"
        class="mb-4"
      >
        <b-card bg-variant="grey" class="mb-2">
          <p>Please submit up to <b>5</b> attachments in support of the nomination.</p>
          <p>Acceptable file formats include the following</p>
          <ul>
            <li>Microsoft Word documents</li>
            <li>Adobe PDF (Portable Document Format) documents</li>
          </ul>
          <!-- Allow up to 5 file uploads -->

          <b-container>
          <b-row
            v-for="(attachment, index) in attachments"
            v-bind:key="index"
            align-h="between"
            class="m-2"
          >

            <b-col v-if="attachment.filename">{{attachment.filename}}</b-col>
            <b-col v-if="attachment.filename">{{attachment.reference}}</b-col>
            <b-col v-if="attachment.filename">
              <b-button
                variant="outline-primary"
                @click="()=>{}">
                Download
              </b-button>
              <b-button
                variant="outline-primary"
                @click="removeAttachment(index)">
                Delete
              </b-button>
            </b-col>

            <b-col v-if="!attachment.filename">
              <b-form-group
                :id="`input-group-attachment-${index}`"
                :label="`Attach File ${index + 1}`"
                :label-for="`input-attachment-${index}`"
              >
                <b-form-file
                  :id="`input-attachment-${index}`"
                  v-model="attachment.file"
                  :state="Boolean(attachment.file)"
                  placeholder="Choose a file or drop it here..."
                  drop-placeholder="Drop file here..."
                  accept=".pdf, .doc, .docx"
                  :description="`Selected file: ${ attachment.file ? attachment.file.name : 'None' }`"
                  @change="update()"
                ></b-form-file>

                <b-button
                  class="m-2"
                  v-if="!attachment.filename"
                  variant="outline-primary"
                  @click="removeAttachment(index)">
                  Remove Attachment
                </b-button>

              </b-form-group>
            </b-col>

          </b-row>
          </b-container>
        </b-card>

        <b-button
          v-if="attachments.length < 5"
          block variant="outline-primary"
          @click="addAttachment()">
          Add Attachment
        </b-button>

      </b-form-group>
    </b-card>

  </div>
</template>

<script>

const noinit =
  {
    file: null,
    filename: '',
    reference: '',
    description: '',
    label: ''
  }

export default {
  name: "attachments-input",
  props: {
    init: Array,
  },
  data() {
    return {
      attachments: this.$props.init.length > 0 ? this.$props.init : [noinit]
    }
  },
  methods: {
    update() {
      this.$emit('update', this.attachments)
    },
    addAttachment(){
      console.log(this.attachments, noinit)
      this.attachments.push(noinit)
      console.log('!!!', this.attachments)
    },
    removeAttachment(counter){
      if (this.attachments.length > 1)
        this.attachments.splice(counter,1);
    }
  }
};
</script>
