<template>
  <div>

    <!-- Form Submit/Reset Buttons -->
    <b-card bg-variant="light" class="mb-3">
      <b-alert v-if="message" show :variant="error ? 'danger' : 'success'">{{message}}</b-alert>
      <b-form-group
        :id="`input-group-submit-${$props.id}`"
        :label-for="`input-submit-${$props.id}`"
        class="mb-1"
        :label="$props.label"
        label-size="lg"
        label-class="font-weight-bold pt-0"
      >
        <b-button
          :disabled="!!$props.disabled"
          class="m-2" type="submit"
          variant="primary">{{$props.submit || 'Submit'}}</b-button>
        <b-button
          v-if="!!$props.reset"
          :disabled="!!$props.disabled"
          class="m-2"
          type="reset"
          variant="dark">Reset</b-button>
      </b-form-group>
    </b-card>

  </div>
</template>

<script>

export default {
  name: "submit-input",
  props: {
    id: String,
    label: String,
    submit: String,
    reset: Boolean,
    disabled: Boolean,
    error: Boolean,
    message: String
  }
};
</script>
