<template>
  <div>
    <b-card bg-variant="light" class="mb-3">

      <b-form-group
        id="input-group-title"
        label="Title of Nomination"
        label-for="input-title"
        label-size="lg"
        label-class="font-weight-bold pt-0"
      >
        <b-form-input
          id="input-title"
          v-model="title"
          placeholder="Enter a title for this nomination"
          @change="update()"
          :state="validation"
        >
        </b-form-input>

        <b-form-invalid-feedback :state="validation">
          Please enter a Nomination Title.
        </b-form-invalid-feedback>

      </b-form-group>

    </b-card>
  </div>
</template>

<script>

export default {
  name: "organization-input",
  props: {
    label: String,
    init: String,
  },
  data() {
    return {
      title: this.$props.init,
    }
  },
  computed: {
    validation () {
      return !!this.title
    }
  },
  methods: {
    update() {
      this.$emit('update', this.title)
    }
  }
};
</script>

