
import api from './api-services';

// localStorage.clear()
const initUser = JSON.parse(localStorage.getItem('user')) || null

// initialize user data
const state = {
  user: initUser
};

const getters = {
  isAuthenticated: state => {
    return !!state.user
  },
  getUser: state => {
    const { guid='', firstname='', lastname='', email='', role='' } = state.user;
    return {
      guid: guid,
      firstname: firstname,
      lastname: lastname,
      email: email,
      role: role
    }
  },
};

const actions = {
  async login({commit}, user) {
    let response = await api.post('auth/login', user)
    await commit('setUser', response.data)
  },
  async logout({commit}){
    await api.post('auth/logout')
    await commit('logout')
  },
  async refresh({ commit }, accessToken) {
    commit('refresh', accessToken);
  }
};

const mutations = {
  refresh(state, accessToken) {
    state.status.loggedIn = true;
    state.user = {...state.user, accessToken: accessToken};
  },
  setUser(state, data){
    console.log(data)
    const { user={} } = data
    const { guid='', firstname='', lastname='', email='', role='' } = user
    const filteredData = {
      guid: guid,
      firstname: firstname,
      lastname: lastname,
      email: email,
      role: role
    }
    localStorage.setItem('user', JSON.stringify(filteredData))
    state.user = filteredData
  },
  logout(state){
    localStorage.clear()
    state.user = null
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};
