<template>
  <div>
    <b-card bg-variant="light" class="mb-3">
      <b-form-group
        id="input-group-evaluation"
        label="Evaluation Considerations"
        label-size="lg"
        label-class="font-weight-bold pt-0"
        class="mb-2"
      >
        <p><strong>The combined sections below must not exceed <u>{{ max.total }}</u> words.</strong></p>
        <b-alert v-if="validation" show variant="success">Current total word count: {{ totalCount }}/{{ max.total }}</b-alert>
        <b-alert v-if="!validation" show variant="danger">Current total word count: {{ totalCount }}/{{ max.total }} (exceeded)</b-alert>

        <b-form-group
          id="input-group-evaluation-summary"
          :label="`Summary (${max.summary} words maximum)`"
          label-size="lg"
          label-for="input-evaluation-summary"
        >
          <slot name="summary_overview">
            <p>In this section, provide a brief statement summarizing the main points of the nomination. The purpose of the summary is to give the adjudicators and judges a condensed and objective account of the main idea(s) and accomplishment(s) of the nomination.</p>
            <p><em>(Note that this section will be used by the Premier’s Awards team for communications purposes if this nomination is selected as a finalist.)</em></p>
          </slot>

          <b-form-textarea
            id="input-evaluation-summary"
            v-model="data.summary"
            placeholder="Enter the text here..."
            rows="5"
            max-rows="6"
            @change="update('summary')"
            :state="summaryValidation"
          >
          </b-form-textarea>
          <b-form-invalid-feedback :state="summaryValidation">
            Summary word count: {{summaryCount}} / {{max.summary}} <span v-if="!validation">(Evaluation word count exceeded)</span>
          </b-form-invalid-feedback>
          <b-form-valid-feedback :state="summaryValidation">
            Summary word count: {{summaryCount}} / {{max.summary}}
          </b-form-valid-feedback>
        </b-form-group>
      </b-form-group>

      <b-form-group
        id="input-group-evaluation-context"
        :label="`Context (${max.context} words maximum)`"
        label-size="lg"
        label-for="input-evaluation-context"
      >
        <slot name="context_overview">
          <p>Briefly describe why the policy or program was developed to provide background and perspective.</p>
        </slot>

        <b-form-textarea
          id="input-evaluation-context"
          v-model="data.context"
          placeholder="Enter the text here..."
          rows="5"
          max-rows="6"
          @change="update('context')"
          :state="contextValidation"
        >
        </b-form-textarea>
        <b-form-invalid-feedback :state="contextValidation">
          Context word count: {{contextCount}} / {{max.context}} <span v-if="!validation">(Evaluation word count exceeded)</span>
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="contextValidation">
          Context word count: {{contextCount}} / {{max.context}}
        </b-form-valid-feedback>
      </b-form-group>

      <b-form-group
        v-if="!!this.$slots.complexity_overview"
        id="input-group-evaluation-complexity"
        label="Complexity"
        label-size="lg"
        label-for="input-evaluation-complexity"
      >
        <slot name="complexity_overview"></slot>
        <b-form-textarea
          id="input-evaluation-complexity"
          v-model="data.complexity"
          placeholder="Enter the text here..."
          rows="5"
          max-rows="6"
          @change="update('complexity')"
          :state="validation"
        >
        </b-form-textarea>
        <b-form-invalid-feedback :state="validation">
          Complexity word count: {{complexityCount}} <span v-if="!validation">(Evaluation word count exceeded)</span>
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validation">
          Complexity word count: {{complexityCount}}
        </b-form-valid-feedback>
      </b-form-group>

      <b-form-group
        v-if="!!this.$slots.approach_overview"
        id="input-group-evaluation-approach"
        label="Approach"
        label-size="lg"
        label-for="input-evaluation-approach"
      >
        <slot name="approach_overview"></slot>
        <b-form-textarea
          id="input-evaluation-approach"
          v-model="data.approach"
          placeholder="Enter the text here..."
          rows="5"
          max-rows="6"
          @change="update('approach')"
          :state="validation"
        >
        </b-form-textarea>
        <b-form-invalid-feedback :state="validation">
          Approach word count: {{approachCount}} <span v-if="!validation">(Evaluation word count exceeded)</span>
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validation">
          Approach word count: {{approachCount}}
        </b-form-valid-feedback>
      </b-form-group>

      <b-form-group
        v-if="!!this.$slots.valuing_people_overview"
        id="input-group-evaluation-valuing-people"
        label="Valuing People"
        label-size="lg"
        label-for="input-evaluation-valuing-people"
      >
        <slot name="valuing_people_overview"></slot>
        <b-form-textarea
          id="input-evaluation-valuing-people"
          v-model="data.valuing_people"
          placeholder="Enter the text here..."
          rows="5"
          max-rows="6"
          @change="update('valuing_people')"
          :state="validation"
        >
        </b-form-textarea>
        <b-form-invalid-feedback :state="validation">
          Valuing People word count: {{valuingPeopleCount}} <span v-if="!validation">(Evaluation word count exceeded)</span>
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validation">
          Valuing People word count: {{valuingPeopleCount}}
        </b-form-valid-feedback>
      </b-form-group>

      <b-form-group
        v-if="!!this.$slots.commitment_overview"
        id="input-group-evaluation-commitment"
        label="Commitment"
        label-size="lg"
        label-for="input-evaluation-commitment"
      >
        <slot name="commitment_overview"></slot>
        <b-form-textarea
          id="input-evaluation-commitment"
          v-model="data.commitment"
          placeholder="Enter the text here..."
          rows="5"
          max-rows="6"
          @change="update('commitment')"
          :state="validation"
        >
        </b-form-textarea>
        <b-form-invalid-feedback :state="validation">
         Commitment word count: {{commitmentCount}} <span v-if="!validation">(Evaluation word count exceeded)</span>
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validation">
          Commitment word count: {{commitmentCount}}
        </b-form-valid-feedback>
      </b-form-group>

      <b-form-group
        v-if="!!this.$slots.contribution_overview"
        id="input-group-evaluation-contribution"
        label="Contribution"
        label-size="lg"
        label-for="input-evaluation-contribution"
      >
        <slot name="contribution_overview"></slot>
        <b-form-textarea
          id="input-evaluation-contribution"
          v-model="data.contribution"
          placeholder="Enter the text here..."
          rows="5"
          max-rows="6"
          @change="update('contribution')"
          :state="validation"
        >
        </b-form-textarea>
        <b-form-invalid-feedback :state="validation">
          Contribution word count: {{contributionCount}} <span v-if="!validation">(Evaluation word count exceeded)</span>
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validation">
          Contribution word count: {{contributionCount}}
        </b-form-valid-feedback>
      </b-form-group>

      <b-form-group
        v-if="!!this.$slots.impact_overview"
        id="input-group-evaluation-impact"
        label="Impact"
        label-size="lg"
        label-for="input-evaluation-impact"
      >
        <slot name="impact_overview"></slot>
        <b-form-textarea
          id="input-evaluation-impact"
          v-model="data.impact"
          placeholder="Enter your text here..."
          rows="5"
          max-rows="6"
          @change="update('impact')"
          :state="validation"
        >
        </b-form-textarea>
        <b-form-invalid-feedback :state="validation">
          Impact word count: {{impactCount}} <span v-if="!validation">(Evaluation word count exceeded)</span>
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validation">
          Impact word count: {{impactCount}}
        </b-form-valid-feedback>
      </b-form-group>

    </b-card>
  </div>
</template>

<script>

import {getWordCount} from '@/services/data-services'

const noinit = {
  summary: '',
  context: '',
  complexity: '',
  approach: '',
  valuing_people: '',
  commitment: '',
  contribution: '',
  impact: ''
}

export default {
  name: "evaluation-input",
  props: {
    init: Object,
  },
  data () {
    return {
      max: {
        total: 1650,
        summary: 150,
        context: 250
      },
      data: Object.keys(this.$props.init).length > 0 ? this.$props.init : noinit
    }
  },
  computed: {
    summaryCount() {
      return getWordCount(this.data.summary)
    },
    contextCount() {
      return getWordCount(this.data.context)
    },
    approachCount() {
      return getWordCount(this.data.approach)
    },
    complexityCount() {
      return getWordCount(this.data.complexity)
    },
    valuingPeopleCount() {
      return getWordCount(this.data.valuing_people)
    },
    commitmentCount() {
      return getWordCount(this.data.commitment)
    },
    contributionCount() {
      return getWordCount(this.data.contribution)
    },
    impactCount() {
      return getWordCount(this.data.impact)
    },
    totalCount() {
      let wordCount = 0;
      Object.keys(this.data).map(key => {
        wordCount += getWordCount(this.data[key])
      })
      return wordCount
    },
    validation() {
      return this.totalCount < this.max.total
    },
    summaryValidation() {
      return this.summaryCount < this.max.summary && this.validation
    },
    contextValidation() {
      return this.contextCount < this.max.context && this.validation
    }
  },
  methods: {
    update() {
      this.$emit('update', this.data)
    }
  }
}
</script>

